body {
        height: auto;
        background-image: url('./christina-deravedisian-we5c6QdfNCw-unsplash.jpg');
    
}

#contactMe {
       background-color: #283B36;
       box-shadow: #283B36 6px 6px 0px 0px;
       border-color: #283B36;
       width: 180px;
       height: 55px;
}
    
    
#contactMe:hover {
        box-shadow: #283B36 3px 3px 2px 2px;
        color: rgba(255, 217, 0, 0.767);
}
    
#contactMe:active {
        box-shadow:rgba(255, 217, 0, 0.767) 2px 2px 1px 1px;
}



#homePic {
        border-radius: 10px!important;
       
}

       
#homeJumbo {
        border-radius: 8px;
        flex-wrap: wrap;
}
       
       
       
.fa-instagram:hover {
       cursor: pointer;
       color: #B9348F;
       border-radius: 7px;
}
       
.fa-youtube:hover {
        cursor: pointer;
        color: red;
}
       
.fa-twitter-square:hover {
        cursor: pointer;
        color: #1DA1F2
}
       
h3 {
        color:#283B36
}
        
hr {
        background-color:rgba(255, 217, 0, 0.767);
}